  }
}

.main-header__logo {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  z-index: 1;
}
@media (max-width: 767px) {
  .main-header__logo {
    bottom: 0;
    transform: translateX(-50%) translateY(100%);
  }
}
@media (min-width: 768px) {
  .main-header__logo {
    top: 0;
  }
}

.main-navigation {
  position: relative;
  text-align: center;
  background-color: #ffffff;
}

.main-navigation__link {
  display: block;
  padding: 17px 0 12px 0;
  font-weight: 700;
  font-size: 18px;
  text-decoration: none;
  color: #ffffff;
  border-top: solid 1px rgba(255, 255, 255, 0.2);
  border-bottom: solid 1px rgba(1, 1, 1, 0.2);
}
.main-navigation__link:hover {
  color: #81b3d2;
}
.main-navigation__link:active {
  color: rgba(0, 0, 0, 0.3);
  fill: rgba(0, 0, 0, 0.3);
}
@media (max-width: 767px) {
  .main-navigation__link:hover {
    background-color: #679ec1;
  }
  .main-navigation__link:active {
    background-color: #5496bd;
    color: rgba(255, 255, 255, 0.3);
  }
  .main-navigation__link:disabled {
    background-color: #e5e5e5;
  }
}
@media (min-width: 768px) {
  .main-navigation__link {
    font-size: 14px;
    color: #000000;
    border: none;
  }
}

.main-navigation__list {
  margin: 0;
  padding: 0;
  list-style: none;
  position: absolute;
  width: 100%;
  min-height: 56px;
  background-color: #81b3d2;
  z-index: 2;
}
@media (max-width: 767px) {
  .main-navigation__list::after {
    position: absolute;
    content: "";
    left: 0;
    bottom: 0;
    width: 100%;
    height: 7px;
    background: linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.2));
  }
}
@media (min-width: 768px) {
  .main-navigation__list {
    padding-left: 50px;
    padding-right: 50px;
    position: relative;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    column-gap: 30px;
    row-gap: 10px;
    background-color: #ffffff;
    z-index: 0;
  }
}

@media (min-width: 768px) {
  .main-navigation__item {
    text-align: start;
  }
  .main-navigation__item:nth-child(3n), .main-navigation__item:nth-child(4n) {
    text-align: end;
  }
}

.main-navigation__item:nth-child(3),
.main-navigation__item:nth-child(7) {
  grid-column: 4/5;
}

.main-navigation__item:last-child .main-navigation__link {
  border-bottom: transparent;
}

@media (min-width: 768px) {
  .main-navigation__link--current {
    color: #766357;
  }
}

.main-navigation__toggle {
  position: absolute;
}
.main-navigation__toggle:hover {
  opacity: 0.6;
}
.main-navigation__toggle:active {
  opacity: 0.3;
}
@media (max-width: 767px) {
  .main-navigation__toggle {
    top: 17px;
    right: 20px;
    z-index: 2;
    display: block;
    width: 23px;
    height: 23px;
    background-color: #ffffff;
    cursor: pointer;
    border: none;
  }
}

@media (max-width: 767px) {
  .main-navigation__list--close {
    display: none;
  }
  .main-navigation__list--close ~ .main-navigation__toggle::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 23px;
    height: 4px;
    background-color: #81b3d2;
    box-shadow: 0 9px 0 0 #81b3d2, 0 18px 0 0 #81b3d2;
  }
}

@media (max-width: 767px) {
  .main-navigation__list--opened ~ .main-navigation__toggle {
    background-color: #81b3d2;
  }
  .main-navigation__list--opened ~ .main-navigation__toggle::before, .main-navigation__list--opened ~ .main-navigation__toggle::after {
    content: "";
    right: 1px;
    top: 9px;
    position: absolute;
    width: 23px;
    height: 4px;
    background-color: #ffffff;
  }
  .main-navigation__list--opened ~ .main-navigation__toggle::before {
    transform: rotate(45deg);
    box-shadow: none;
  }
  .main-navigation__list--opened ~ .main-navigation__toggle::after {
    transform: rotate(-45deg);
  }
}

.main-navigation__list--nojs {
  position: relative;
}
@media (max-width: 767px) {
  .main-navigation__list--nojs {
    display: block;
  }
}
.main-navigation__list--nojs ~ .main-navigation__toggle {
  display: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

